img {
    display: block;
    max-width: 100%;
    height: auto;
  }
  .gallery {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: -10px;
    margin-left: -10px;
    margin-bottom: 10px;
  }
  
  .photo-card {
    margin-top: 10px;
    margin-left: 10px;
  
    flex-basis: calc((100% - 40px) / 4);
  }
  
  .photo-card__image {
    width: 100%;
    height: 200px;
  }
  
  .container {
    width: 1200px;
    padding-left: 15px;
    padding-right: 15px;
    margin: auto;
  }
  .gallery-item {
    display: block;
    text-decoration: none;
    color: #000000;
  }
  
  .info {
    display: flex;
  }
  
  .info-item {
    font-size: 13px;
    text-align: center;
  }
  
  .hidden {
    display: none;
  }
  
  .load-more {
    border: 0;
    min-width: 100px;
    padding-top: 10px;
    padding-bottom: 10px;
  
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    border-radius: 4px;
  
    color: #ffffff;
    background-color: #2196f3;
  
    cursor: pointer;
  
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
  }

  .load-more:hover,
    .load-more:focus {
        background-color: #188ce8;
    }
  
  .load-more_wrapper {
    text-align: center;
  
    margin-bottom: 200px;
  }
  
  .search-form {
    text-align: center;
    padding-top: 15px;
    padding-bottom: 15px;
    margin-bottom: 10px;
  
    border-bottom: 3px solid #f6f6f6;
  }
